* {
  box-sizing: border-box;
}
body{
  margin: 0 auto;
  background-color: #0B2434;
  padding: 20px;
  display: flex;
  justify-content: center;
}
main{
  background-color: #F5F5F5;
  height: 400px;
  width: 800px;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.dice-container{
  display: grid;
  grid-template: auto auto / repeat(5,1fr);
  gap: 20px;
}
.die-face{
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.die-num{
  font-size: 2rem;
}
button{
  height: 50px;
  width: 130px;
  border: none;
  border-radius: 6px;
  background-color: #5035FF;
  color: #F5F5F5;
  font-size: 1.2rem;
  margin-top: 25px;
  cursor: pointer;
}
button:focus{
  outline: none;
}
button:active{
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}
